<template>
  <div class="tabbarcontent">
   
    <div class="footBox">
      <div class="language"></div>
      <div class="navFoot">
        <div class="logo">
          <img src="../assets/img/logo.jpg" alt  />
        </div>
        <div
          v-for="item in tabBarArr"
          ref="btn"
          class="tab-item"
          :class="{ bigItem: item.id === id}"
          :key="item.id"
          @click="switchTo(item)"
        >
          <span :class="{ on: item.id === id }">{{ item.name }}</span>
        </div>
      </div>
      <div class="language">
        <span @click="language(1)">中文</span>/
        <span @click="language(2)">English</span>
      </div>
    </div>

    <transition name="fade-three" mode="out-in">
      <router-view v-if="show" />
    </transition>

    <div class="footer" id="foun">
      <div class="content">
        <div class="left">
           <ul>
            <li ><span class="one" :class="lang?'':'ones'">{{$t('home.合作单位')}}</span> <span>{{$t('navigate.基金会')}}</span></li>
          </ul>
          <ul>
            <li><span class="one" :class="lang?'':'ones'">{{$t('home.乐读中国')}}</span><span>{{$t('home.逸仙资本')}}</span></li>
            <li><span class="one" :class="lang?'':'ones'"></span>{{$t('home.嘉尚基金')}}</li>
            <li><span class="one" :class="lang?'':'ones'"></span>{{$t('home.天引资本')}}</li>
            <li><span class="one" :class="lang?'':'ones'"></span>{{$t('home.天弘基金')}}</li>
          </ul>
        </div>
        <ul>
          <li>{{$t('home.在线客服')}}</li>
          <li>010 - 4567 - 8987</li>
          <li>{{$t('home.周一至周日')}} 9:00 - 22:00?</li>
        </ul>
      </div>
      <div>@2020 ledu All Rights Reserved. (湘ICP备20088308号-1) </div>
    </div>

    <div class="topAnchor" @click="topAnchor" >
      <div :class="tops?'but':''"> TOP </div>
    </div>
  </div>
</template>

<script>
import diffusion from "@/mixin/diffusion.js";
export default {
  name: "tabbar",
  mixins: [diffusion],
  data() {
    return {
      show: true,
      lang: false,
      id:0,
      files:'',
      tops:false,
      tabBarArr: [
        {
          id: 0, //ID号
          name: this.$t("navigate.首页"), //名称
          idname:'home'
        },
        {
          id: 1, //ID号
          name: this.$t("navigate.项目介绍"), //名称
          idname:'intr'
        },
        {
          id: 2, //ID号
          name: this.$t("navigate.使命愿景"), //名称
          idname:'visi'
        },
        {
          id: 3, //ID号
          name: this.$t("navigate.项目优势"), //名称
          idname:'adva'
        },
        {
          id: 4, //ID号
          name: this.$t("navigate.应用场景"), //名称
          idname:'apps'
        },
        {
          id: 5, //ID号
          name: this.$t("navigate.团队介绍"), //名称
          idname:'team'
        },
        {
          id: 6, //ID号
          name: this.$t("navigate.发展规划"), //名称
          idname:'plan'
        },
        {
          id: 7, //ID号
          name: this.$t("navigate.基金会"), //名称
          idname:'foun'
        },
        {
          id: 8, //ID号
          name: this.$t("navigate.白皮书"), //名称
        }
      ]
    };
  },
  methods: {
    switchTo(val) {
      this.id = val.id;
      if(val.id < 8){
        let top = document.querySelector('#'+val.idname).offsetTop;
        window.scrollBy({
          top:top,
          left:0,
          behavior: "smooth" 
        });
      }else if(val.id == 8){
        let file = '';
        if(this.lang){
          file = '../assets/pdf/LOD白皮书.pdf';
        }else file = '../assets/pdf/LOD Chain Project white paper.pdf';
        
        window.open(`/static/pdf/web/viewer.html?file=${file}`);
      }
    },
    language(val) {
      if (val == 1) {
        localStorage.setItem("lang", "zh");
      } else {
        localStorage.setItem("lang", "en");
      }
      window.location.reload();
    },
    topAnchor(){
      window.scrollTo({
        top:0,
        left:0,
        behavior: "smooth" 
      });
    },
    windowScroll(){
      // 滚动条距离页面顶部的距离
      // 以下写法原生兼容
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(scrollTop>0)this.tops = true;
      else this.tops = false;
    }
  },
  mounted() {
    let lang = localStorage.getItem('lang');
    if(lang == 'en'){
      this.lang = false;
    }else this.lang = true
    window.addEventListener('scroll', this.windowScroll)
  }
};
</script>

<style scoped lang="scss">

.bigItem {
  z-index: +1;
}

.footBox {
  min-width: 1300px;
  width: 100%;
  height: 70px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 1);
  display: flex;
  align-items: center;
  box-shadow:2px 3px 14px 0px rgba(240,240,240,0.8);
}
.navFoot {
  flex: 0.6;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
}
.tab-item {
  height: 60px;
  line-height: 60px;
  align-items: center;
}
.tab-item > span {
  font-size: 1em;
  font-weight: 500;
  color: #333333;
  padding: 0px 16px;
  white-space: nowrap;
  cursor: pointer;
  font-family: Source Han Sans CN;
}
.logo {
  padding-right: 70px;
}
.logo > img {
  width: 80px;
  height: 70px;
}
.language {
  flex:0.2;
  font-size: 1em;
  font-weight: 500;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
  font-family: Source Han Sans CN;
}
.tab-item > .on {
  color: #4285f4;
}
.footer{
  cursor: default;
  min-width: 1300px;
  width: 100%;
  height:239px;
  background:rgba(73,106,249,1);
  display: flex;
  flex-direction: column;
  align-items: center;
  color:rgba(255,255,255,1);
  .content{
    width: 65%;
    height: 202px;
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    .left{
      &>ul:nth-of-type(1){
        margin-bottom: 20px;
      }
      ul{
        .one{
          width: 110px;
        }
        .ones{
          width: 150px;
        }
        li{
          display: flex;
        }
        span{
          display: block;
        }
      }
    }
    li{
      font-size:1.3em;
      font-family:Source Han Sans CN;
      font-weight:none;
      line-height:24px;
      text-align: left;
    }
  }
}
.topAnchor{
  position:fixed;
  right: 57px;
  bottom: 50px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  div{
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: white;
    color:rgba(211,211,211,1);
    line-height: 45px;
    text-align: center;
    opacity: 1;
    font-size: 12px;
    font-weight: normal;
    transition: all 1s;
    box-shadow:0px 0px 1px 1px rgba(211,211,211,1);
    margin-bottom: -150px;
  }
  .but{
    margin-bottom:1px!important;
  }
  div:hover{
    opacity: 0.3;
  }
}
</style>